import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';

//Page Module
import { PagesModule } from '../../../../pages/pages.module'

import { ConfmanagerSettingsModule } from '../confmanager-settings.module'

import { FilegroupsComponent } from './filegroups.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';

@NgModule({
  declarations: [
    FilegroupsComponent,
    AddComponent,
    EditComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    PagesModule,
    ConfmanagerSettingsModule,
    RouterModule.forChild([
      {
        path: '',
        component: FilegroupsComponent
      },
      {
        path: 'add',
        component: AddComponent
      },
      {
        path: 'edit/:id',
        component: EditComponent
      },
    ]),
  ]
})
export class ConfmFilegroupsModule { }
