import { Component, OnInit } from '@angular/core';

//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Query
import { ConfmGroup } from '../../../../../partials/layout/tacgui/_forms/confmanager/confm-group-form/confm-group.model'
//Service
import { FilegroupService } from '../filegroup.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new ConfmGroup,
    old_data: new ConfmGroup
  }

  constructor(
    private toastr:ToastrService,
    private service: FilegroupService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(params.get('id'))
    });
  }

  fillForm(id:string){
    console.log(id)
    this.service.get(id).subscribe( data => {
      console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      this.formParameters.data = this.makeClone(data)
      this.formParameters.old_data = this.makeClone(data)


      console.log(this.formParameters.data)
      this.loadingForm.next(false)

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)
    // let old_data = this.makeClone(this.formParameters.old_data)

    // console.log(this.formParameters.data)
    // console.log(JSON.stringify(data) ,JSON.stringify(this.formParameters.old_data))
    if (JSON.stringify(data) == JSON.stringify(this.formParameters.old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }

    console.log(data)

    this.service.save(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('Group Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
