import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/confmanager/groups/';

@Injectable({
  providedIn: 'root'
})
export class FilegroupService {

  constructor(private http: HttpClient) {}

  add(group:{}) {
    return this.http.post<any>(API_URL + '/add/', group)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  get(id:number | string) {
    const params = new HttpParams()
    .set('id', id.toString());
    return this.http.get<any>(API_URL + '/edit/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.group
        } )
      )
  }

  save(group:{}) {
    return this.http.post<any>(API_URL + '/edit/', group)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

}
